.blurred {

  /* Any browser which supports CSS3 */
  filter: blur(3px);

  /* Firefox version 34 and earlier */
  filter: url("/assets/blur.svg#gaussian_blur");

  /* Webkit in Chrome 52, Safari 9, Opera 39, and earlier */
  -webkit-filter: blur(3px);
}

:host {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  height: 100%;
  min-height: 0;
}

.no-scrollbar {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.multi-connect-hint {
  position: relative;
  opacity: 0.8;
  transition: opacity 200ms ease-in-out, box-shadow 3.5s ease-in-out;
  animation: hintPulse 3.5s ease-in-out infinite;
}

.multi-connect-hint:hover {
  opacity: 1;
}

.multi-connect-hint::after,
.multi-connect-hint::before {
  pointer-events: none;
}


@keyframes hintPulse {
  0% {
    box-shadow: 0 0 0 0 rgba(91, 71, 251, 0.25);
  }
  50% {
    box-shadow: 0 0 0 8px rgba(91, 71, 251, 0.05);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(91, 71, 251, 0);
  }
}

@media (prefers-reduced-motion: reduce) {
  .multi-connect-hint {
    animation: none;
  }
}
